let article2 = {
    id: 2,
    header: "Atomkraft er ikke en mirakellsning p klimasprgsmlet",
    content: 
    [`Frygten i befolkningen, der associerer atomkraft med emner som Tjernobyl- og Fukushima ulykkerne, atomvben og Kold Krigstid, og radioaktivt affald, forklares ofte som vrende den primre rsag til at de mirakulse atomkraftvrker endnu ikke dominerer verdens energiforsyning. Det hres ofte, at kernekraft er en sikker, ren og billig energikilde, men er der i virkeligheden teknologiske begrnsninger, der gr at atomkraftvrker ikke giver mening at prioritere for Danmark i energimssigt knebne tider?`,
    `Jovist udleder atomkraftvrker ikke CO2 ved produktion af strm. Derfor kan det iflge mange give mening at prioritere atomkraft, som en del af klimakampen. Det skriver artiklen Kan atomkraft redde verden? fra videnskab.dk ogs gennem et citat fra professor Jens Hesselbjerg Hansen fra Niels Bohr Instituttet: Kigger man udelukkende p atomkraft fra et klimaperspektiv, er det ikke svrt at flde en dom  isoleret set er atomkraft godt for klimaet []. Derudover er der ogs den CO2-kvivalente udledning af vanddamp og varme som et atomkraftvrk producerer under betjening, samt CO2-udledningen ved udvinding og raffinering af atombrndsel. Professor i miljingenirvidenskab, Mark Z. Jacobson fra Stanford University, skriver mere specifikt i et uddrag fra bogen 100% Clean, Renewable Energy and Storage for Everything: Even existing plants emit due to the continuous mining and refining of uranium needed for the plant. However, all plants also emit 4.4 g-CO2e/kWh from the water vapor and heat they release. This contrasts with solar panels and wind turbines, which reduce heat or water vapor fluxes to the air by about 2.2 g-CO2e/kWh for a net difference from this factor alone of 6.6 g-CO2e/kWh.`,
    `Et andet af de mest prominente argumenter for indfrslen af atomkraft i Danmark, er at det kan levere strm til basisbelastningen p elnettet. Det nvner videnskab.dk ogs i den frnvnte artikel: Strmmen kan reguleres i modstning til eksempelvis vind og solenergi, som er afhngig af, at vinden blser, eller af at solen skinner. El-produktion fra atomkraft kan dermed flge eftersprgslen p elektricitet. Dette er utvivlsomt den strste svaghed ved vind- og solenergi, som der mangler en klar lsning p. Dog er der en rkke faktorer, som Mark Z. Jacobson kommer ind p i sin bog. Den frste er prisen p eksempelvis vindenergi sammenlignet med atomkraft: [] a new nuclear power plant costs 2.3 to 7.4 times that of an onshore wind farm (or utility PV farm). Derfor ville man kunne have et stort overskud af elkapacitet ved brug af vind- og solenergi, hvis man investerede den samme mngde penge. Den anden faktor er, at man kan bruge nogle af de penge man sparer p at bygge vind- og solenergi p at investere i batterifarme og Power-to-X teknologier. Dette kan hjlpe med at gre vind og solenergi til en mere plidelig energikilde, som ikke er utilregnelig. Fasihi og Breyer skriver i deres videnskabelige artikel Baseload electricity and hydrogen supply based on hybrid PV-wind power plants flgende: The hourly analyses show that hybrid PV-wind [sol og vind] power plants can provide BLEL [baseload electricity] at affordable costs in most regions around the world, if coupled with proper daily and long-term balancing technologies.. Mark Diesendorf, professor ved Institute of Environmental Studies p UNSW, skriver desuden om en rkke tiltag man kan gre for at bredygtige energikilder kan imdekomme basisbelastnings eftersprgslen i artiklen Renewable energy can provide baseload power - heres how: The baseload issue can be solved by reducing baseload demand, having some renewable energy sources that can supply baseload power and increasing the proportion of flexible peakload plant in the generating mix. Alts har han de flgende tre argumenter:
    1.	Snke eftersprgslen af basisbelastningen p strm og udligne eftersprgslen om natten og dagen
    2.	Gre brug af vand- og geotermisk energi hvor det er geografisk muligt, og derudover bioelektricitet til at generere strm nr vind- og solenergi ikke er nok
    3.	Udnytte et flles energinet over strre geografiske omrder. Det gr vind- og solenergi mere forudsigeligt og mindre svingende`,
    `Alt dette er de udvalgte kilders syn p atomkraft sammenlignet med bredygtige energikilder. Ud fra dette vil jeg argumentere for, hvorfor vi ikke skal bruge tid og penge p at bygge atomkraftvrker i Danmark.`,    
    `Den frste rsag er ret benlys. Der er ud fra ovenstende referat af kilderne, store teknologiske udfordringer og begrnsninger med atomkraft, som ofte ikke bliver nvnt i debatten. Debatten ender tit om patosladede ekstremer, der p den ene side bruger Tjernobyl som et radioaktivt fugleskrmsel, og p den anden side bruger eksempelvis Frankrigs store succes med atomkraft. Nr Atomkraft - ja tak siden bliver konfronteret med de teknologiske udfordringer ved atomkraft, ender det ofte med en hbefuld tanke om nye Generation IV reaktorer som eksempelvis flydende salt reaktorer. Disse kan i teorien gre det billigere og mere klimavenligt at producere atomkraft. Danske virksomheder som Seaborg og Copenhagen Atomics arbejder p at realisere denne teknologi. Det kan blandt andet gre atomkraft mere klimavenlig, ved at det krver mindre brndsel og dermed mindre udledning af CO2 under raffinering og udvinding. De kan ogs benytte atomaffald fra andre reaktorer som brndsel. Der er ogs mindre CO2 kvivalent opvarmning ved udledning af damp og varme, ved brug af Generation IV reaktorer som smeltet saltreaktorer - og der er ikke problemet om variabel strmproduktion som ved vind- og solenergi.`,
    `Problemet med Generation IV reaktorer, er at klimakampen har en meget klar tidshorisont, og det giver ikke mening at vente med at videnskaben kommer med et magisk, mirakulst lsningsforslag som eksempelvis kunne vre Generation IV reaktorer - i modstning til hvad konsensus er i den siddende regering om skaldte hockeystavslsninger. De nyeste atomkraftvrker har taget mellem 10 og 20 r fra planlgning til de bliver tndt. Det er ambitist nok at tro vi i et land, der, desvrre, ikke har kompetencerne og den ndvendige viden til at bygge det frste atomkraftvrk kan holde os indenfor denne tidsramme, men sagen er den at selv hvis vi nr denne tidsramme, er det stadig for sent. Parisaftalen har et ml om at holde os under allerhjest 2 graders global opvarmning i 2100. Med et fald p 5% rligt i CO2 udledning skulle vi vre i gang allerede i r. Derfor kan det kun g for langsomt. Vi kan ikke vente med at videnskaben kommer med Generation IV reaktorer. Jovist ville disse muligvis lse mange af de teknologiske og konomiske ulemper der er ved atomkraft sammenlignet med vind- og solenergi, men hvis disse frst er klar i 2030, og der derefter gr 10 til 20 r for at det kan blive implementeret i en ordentlig skala, er vi langt bag tidsplanen. Til sammenligning vil de europiske lande omkring Nordsen i 2030 allerede producere 65 GW elektricitet fra havvindmller, hvilket svarer til over en firedobling af den strm, der bliver produceret af havvindmller i Nordsen i dag. Det er disse aftaler, der er realistiske, ikke nye uafprvede atomkraftvrker, der frst muligvis kunne vre klar fra 2040. Vindmller er teknologi som danske virksomheder er specialiserede i, og en investering i disse danske virksomheder giver ogs langt mere mening for samfundskonomien, end at bruge de 1000 mia. kr denne havvindmlle-aftale har af budget, p udenlandske virksomheder, der har den ndvendige viden om atomkraft.`,
    `Min kritik af atomkraft m endelig ikke forsts som om at kul-, olie-, og gaskraftvrker er at foretrkke over atomkraftvrker. Det er en komplet misforstet holdning. Indtil nu, har alle mine sammenligninger vret med primrt vind- og solenergi, men ogs vand- og geotermiskenergi og biobrndsel. Det er min holdning, at debatten om atomkraft kontra fossilebrndstoffer er udkrt i den offentlige debat, og at ingen af argumenterne for at foretrkke fossilebrndstoffer over atomkraft holder. Disse argumenter er nemlig primrt omkring omrderne atomaffald og atomulykker. Alligevel vil jeg gerne adressere disse argumenter, men i en kontekst af atomkraft kontra vind- og solenergi. Atomaffald er i hj grad et mindre problem, en mange antager, og strstedelen af lande, der gr brug af atomkraft i dag, har langtidsholdbare lsninger til opbevaring af atomaffald - det glder fx Finland. Det er af min overbevisning, at hvis Danmark skulle indfre atomkraft, ville det ogs vre muligt at finde en realistisk lsning p sprgsmlet om atomaffald. Det nste sprgsml er ulykker p atomkraftvrker. Moderne reaktorer som vi ville skulle bygge i Danmark, er utrolig sikre og p ingen mde at sammenligne med Tjernobyl eller Fukushima. Dog, selvom ulykker p atomkraftvrker ikke er et stort problem i et stabilt land som Danmark, er det vrd at overveje konsekvenserne af det ansvar det krver at opretholde et atomkraftvrk. Der er i min optik tre primre trusler angende sikkerheden af atomkraftvrker. Den frste er naturkatastrofer. Dem er vi heldigvis meget sikre mod i Danmark, og det er af min overbevisning at et atomkraftvrk i Danmark ville vre fuldstndig sikret mod de relativt sm naturlige begivenheder, der er i Danmark. Den nste er terrorisme. Atomkraftvrker udgr et stort og benlyst terrorml. Derfor er det ndvendigt at bruge mange penge p sikkerheden ved et atomkraftvrk samt opbevaringen af atomaffald i Danmark. Skulle en terrorgruppe vlge at mlrette sig mod et atomkraftvrk p dansk grund, er det muligt at det vil have meget delggende konsekvenser for et lille og ttbebygget land som Danmark. Den tredje er krig. Selvom krig i Danmark trods alt er meget usandsynlig, skal man ikke kigge langt vk for at se, hvor stort et ansvar et atomkraftvrk er i en krigssituation. Det er en stor byrde for Ukraine at opretholde sikkerheden ved deres atomkraftvrker under krigen, og man koncentrerer desuden meget af energiproduktionen p et meget lille geografisk omrde. Derfor er det vigtigt at opretholde sikkerheden bde p grund af risikoen for radioaktivt udslip samt for at sikre strm til store dele af landet. Vind- og solenergi derimod ville ikke udgre den samme forsvars- og sikkerhedsmssige byrde - bde i tilflde af krig, terrorisme og naturkatastrofer.`,
    `Sprgsmlet om basisforsyning er det strste argument mod brugen af vind- og solenergi fremfor atomkraft. Vind- og solenergi afhnger selvflgelig af henholdsvis vind og sol, men i en tid hvor vi har et utrolig veludbygget elnet i Europa, er det et klart mindre problem end man skulle tro. Som nvnt i referatet af Mark Diesendorfs artikel, er der mange veje der sammen kan fre til at bredygtige energikilder kan dkke basisforsyningen med hj plidelighed. Selvflgelig er den nuvrende basisforsyning af primrt fossilebrndstoffer et problem. Derfor krver det at vi investerer i Power to X lsninger der kan lagre strmmen fra havvindmller og solcelleparker i eksempelvis hydrogen, eller at vi investerer i batteriteknologi, der kan udligne forsyningen af strm fra vind- og sol nr det blser og nr det ikke blser. Forskellen er, at det er teknologi der eksisterer i dag, og kunne indfres hurtigt.`,
    `Gr alt dette s atomkraft til en ligegyldig spiller i klimadebatten? I Danmark - ja. Scenarierne med atomkraft er enten at f en middelmdig lsning, der eksisterer i dag, men mske kan tndes om 10 til 20 r, eller at vente et uvist antal r, mske i omegnen af et rti, p at Generation IV reaktorer er realiseret, og s derefter en rkke r for at det er udbygget til et niveau, der kan erstatte nuvrende fossilebrndstoffer. Det er, som nvnt fr, for uambitist, urealistisk og en drlig undskyldning for ikke at gre noget i dag. Derfor skal vi hellere investere i vind- og solenergi, som mske ikke altid kan levere strm Danmark hvis det ikke blser nok, men at det indgr i det Europiske elnet, hvor der ogs kan indg atomkraft. Atomkraftvrker er som argumenteret for i denne essay, langt at foretrkke over fossilebrndstoffer, og lukningen af atomkraftvrker i Europa er kontraproduktiv i et raserende klimakaplb mod naturen. Det handler ikke om at Danmark bliver afhngig af grn strm produceret af atomkraftvrker i andre lande, som nogle ville sige, men at Europa sammen benytter sig af forskellige bredygtige energikilder afhngig af de geografiske muligheder. I Danmark har vi meget vind og hav, og derfor giver det mening for os at investere i havvindmller i Nordsen. I mellemtiden kan vi investere i den ndvendige forskning det vil krve at f fusionsreaktorer eller Generation IV reaktorer i Danmark, hvis det til den tid er billigere og teknologisk mere attraktivt end vind- og solenergi. Er atomkraft bedre end kul? Ja. Skal vi investere flere penge i forskningen af atomkraft? Ja. Skal vi have traditionel atomkraft i Danmark? Nej, det er for dyrt og langsomt sammenlignet med vind- og solenergi.`,
],
    author: "Anton Irvold",
    time: new Date(2022, 4, 29),
    references: references2,
}

let articles = [article3, article2, article1];

function Emner() {
    return (
        <div>
            {articles.map((article, index) => {
                if ("references" in article) {
                    return (<Article article={article} references={article.references}/>);
                } else {
                    return (<Article article={article} />)
                };
            })
            }
        </div>
    );
}

export default Emner;